	module.exports = {"pathContext":{"docHtml":"<h1>RBAC</h1>\n<h2>Overview</h2>\n<p>The feature RBAC allows restricting access to ArgoCD resources. ArgoCD does not have own user management system and has only one built-in user <code>admin</code>. The <code>admin</code> user is a\nsuperuser and it has full access. RBAC requires configuring <a href=\"./sso.html\">SSO</a> integration. Once <a href=\"./sso.html\">SSO</a> is connected you can define RBAC roles and map roles to groups.</p>\n<h2>Configure RBAC</h2>\n<p>RBAC configuration allows defining roles and groups. ArgoCD has two pre-defined roles: role <code>role:readonly</code> which provides read-only access to all resources and role <code>role:admin</code>\nwhich provides full access. Role definitions are available in <a href=\"../util/rbac/builtin-policy.csv\">builtin-policy.csv</a> file.</p>\n<p>Additional roles and groups can be configured in <code>argocd-rbac-cm</code> ConfigMap. The example below custom role <code>org-admin</code>. The role is assigned to any user which belongs to\n<code>your-github-org:your-team</code> group. All other users get <code>role:readonly</code> and cannot modify ArgoCD settings.</p>\n<p><em>ConfigMap <code>argocd-rbac-cm</code> example:</em></p>\n<pre><code class=\"language-yaml\">apiVersion: v1\ndata:\n  policy.default: role:readonly\n  policy.csv: |\n    p, role:org-admin, applications, *, */*\n    p, role:org-admin, applications/*, *, */*\n\n    p, role:org-admin, clusters, get, *\n    p, role:org-admin, repositories, get, *\n    p, role:org-admin, repositories/apps, get, *\n\n    p, role:org-admin, repositories, create, *\n    p, role:org-admin, repositories, update, *\n    p, role:org-admin, repositories, delete, *\n\n    g, your-github-org:your-team, role:org-admin\nkind: ConfigMap\nmetadata:\n  name: argocd-rbac-cm\n</code></pre>\n<h2>Configure Projects</h2>\n<p>Argo projects allow grouping applications which is useful if ArgoCD is used by multiple teams. Additionally, projects restrict source repositories and destination\nKubernetes clusters which can be used by applications belonging to the project.</p>\n<h3>1. Create new project</h3>\n<p>Following command creates project <code>myproject</code> which can deploy applications to namespace <code>default</code> of cluster <code>https://kubernetes.default.svc</code>. The valid application source is defined in the <code>https://github.com/argoproj/argocd-example-apps.git</code> repository.</p>\n<pre><code>argocd proj create myproject -d https://kubernetes.default.svc,default -s https://github.com/argoproj/argocd-example-apps.git\n</code></pre>\n<p>Project sources and destinations can be managed using commands</p>\n<pre><code>argocd project add-destination\nargocd project remove-destination\nargocd project add-source\nargocd project remove-source\n</code></pre>\n<h3>2. Assign application to a project</h3>\n<p>Each application belongs to a project. By default, all application belongs to the default project which provides access to any source repo/cluster. The application project can be\nchanges using <code>app set</code> command:</p>\n<pre><code>argocd app set guestbook-default --project myproject\n</code></pre>\n<h3>3. Update RBAC rules</h3>\n<p>Following example configure admin access for two teams. Each team has access only two application of one project (<code>team1</code> can access <code>default</code> project and <code>team2</code> can access\n<code>myproject</code> project).</p>\n<p><em>ConfigMap <code>argocd-rbac-cm</code> example:</em></p>\n<pre><code class=\"language-yaml\">apiVersion: v1\ndata:\n  policy.default: \"\"\n  policy.csv: |\n    p, role:team1-admin, applications, *, default/*\n    p, role:team1-admin, applications/*, *, default/*\n\n    p, role:team1-admin, applications, *, myproject/*\n    p, role:team1-admin, applications/*, *, myproject/*\n\n    p, role:org-admin, clusters, get, *\n    p, role:org-admin, repositories, get, *\n    p, role:org-admin, repositories/apps, get, *\n\n    p, role:org-admin, repositories, create, *\n    p, role:org-admin, repositories, update, *\n    p, role:org-admin, repositories, delete, *\n\n    g, role:team1-admin, org-admin\n    g, role:team2-admin, org-admin\n    g, your-github-org:your-team1, role:team1-admin\n    g, your-github-org:your-team2, role:team2-admin\nkind: ConfigMap\nmetadata:\n  name: argocd-rbac-cm\n</code></pre>","docPath":"argo-cd/docs/rbac","proj":"argo-cd"}}

